/*!
 * Setting screen UI for ga-communicator.
 *
 * @deps dashicons
 */.ga-heading span{vertical-align:middle;font-size:1em;width:1em;height:1em}.ga-error{color:#e24949;font-weight:bold}@keyframes ga-rotation{0%{transform:rotate(0deg)}50%{transform:rotate(270deg)}100%{transform:rotate(360deg)}}.ga-setting-row{position:relative}.ga-setting-row .dashicons{display:none}.ga-setting-row.loading .dashicons{color:#ddd;display:inline-block;animation:ga-rotation 1s ease-in-out infinite;vertical-align:middle}.ga-setting-row .notice{position:relative}.ga-setting-table{table-layout:auto;border-collapse:collapse;width:100%;margin:1em 0}.ga-setting-table th,.ga-setting-table td{border:1px solid #ddd;padding:0.5em}.ga-setting-example{padding:1em;font-size:small;background:#ddd;display:none}.ga-setting-example.toggle{display:block}
